body {
	// The scroll is blocked

	// The site is downloaded
	.loaded & {
	}
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	@supports (overflow: clip) {
		overflow: clip;
	}
	// We press Footer
	> main {
		flex: 1 1 auto;
	}
	// Fix for sliders
	> * {
		min-width: 0;
	}
}

//========================================================================================================================================================

.ibg {
	position: relative;
	overflow: hidden;
	width: 100%;
	&__img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
}

.font-bold {
	font-weight: 700;
}
.color {
	&-light-blue {
		color: var(--blue-color-200);
	}
	&-blue {
		color: var(--brand-color);
	}
	&-dark--blue {
		color: var(--blue-color-700);
	}
	&-gray {
		color: var(--gray-color);
	}
}

.s-title {
	&--hero {
		@include adaptiveValue("font-size", 51, 24, 0, 1440, 768);
		line-height: 1.26;
		font-weight: 700;
		@media (max-width: $mobile) {
			@include adaptiveValue("font-size", 33, 24, 1, 641, 402);
		}
	}
	&--main {
		@include adaptiveValue("font-size", 48, 25, 0, 1440, 768);
		line-height: 1.43;
		font-weight: 600;
		@media (max-width: $mobile) {
			@include adaptiveValue("font-size", 30, 25, 1, 641, 402);
		}
	}
}

.s-subtitle {
	line-height: 2;
	@include adaptiveValue("font-size", 27, 13, 0, 1440, 641);
	@media (max-width: $mobile) {
		@include adaptiveValue("font-size", 20, 10, 1, 641, 402);
	}
}

.button-color {
	background: var(--brand-color);
	color: var(--white-color);
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border-radius: toRem(29);
	min-height: toRem(58);
	padding: toRem(5) toRem(34);
	transition: background 0.2s ease-in-out;
	font-size: toRem(23);
	@media (any-hover: hover) {
		&:hover {
			background: var(--blue-color-700);
		}
	}
	@media (max-width: $tablet) {
		font-size: toRem(18);
		min-height: toRem(40);
		padding: toRem(5) toRem(21);
	}
	@media (max-width: $mobile) {
		font-size: toRem(12);
		min-height: toRem(30);
		padding: toRem(5) toRem(17);
	}
}

//========================================================================================================================================================

.page {
	// .page__hero

	&__hero {
	}
	&__about {
		padding-top: toRem(80);
		padding-bottom: toRem(80);
		@media (max-width: $mobile) {
			padding-top: toRem(50);
			padding-bottom: toRem(50);
		}
	}
	// .page__programs

	&__programs {
	}
}
//========================================================================================================================================================

.content {
	display: flex;
	flex-direction: column;
	gap: toRem(5);
	&__sub-title {
		color: var(--brand-color);
		@include adaptiveValue("font-size", 26, 13);
		font-weight: 500;
		&_white {
			color: var(--white-color);
		}
	}
	// .content__title

	&__title {
		@include adaptiveValue("font-size", 44, 24);
		font-weight: 600;
		line-height: 1.3;

		&_big {
			@include adaptiveValue("font-size", 47, 30);
		}
	}

	// .content__text

	&__text {
		line-height: 1.42;
		@include adaptiveValue("font-size", 20, 14);
	}

	// .content__button

	&__button {
	}
	&_center {
		text-align: center;
	}
	&_white {
		color: var(--white-color);
	}
	&_left {
		text-align: left;
	}
}

.text {
	line-height: 1.65;
	p {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}
}

.title-decor {
}

.ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&_contain {
		img {
			object-fit: contain;
		}
	}
}

.sr-only {
	position: absolute;
	width: toRem(1);
	height: toRem(1);
	padding: 0;
	margin: toRem(-1);
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}

._decor-bg {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: url("@img/bg-decor.png") 0 0 / cover no-repeat;
	}
}
