// .figure-image

.image-figure {
   max-width: toRem(562);
   position: relative;
   &::before {
      content: "";
      position: absolute;
      pointer-events: none;
      z-index: 3;
   }

   &--v1 {
      &::before {
         top: 0.5%;
         left: 0%;
         width: 101%;
         height: 100%;
         background: url("@img/media-image-block/border-01.svg") center center / contain no-repeat;
      }
   }

   &--v2 {
      &::before {
         top: 0%;
         left: -0.3%;
         width: 100%;
         height: 100%;
         background: url("@img/media-image-block/border-02.svg") center center / contain no-repeat;
      }
   }
   @media (max-width: toEm(641)) {
      max-width: 92%;
      margin: 0 auto;
   }

   // .figure-image__img

   &__img {
      z-index: 1;
      clip-path: polygon(0 0, 100% 6%, 98.5% 96.6%, 2.5% 100%);
      padding-bottom: 75.039494%; /* 475/633 */
      .image-figure--v2 & {
         clip-path: polygon(0 5.5%, 100% 0%, 97.3% 100%, 1.2% 96.5%);
      }
   }
}
