.treatment {
	padding-top: toRem(37.5);
	padding-bottom: toRem(37.5);
	// .treatment__container

	&__container {
		display: grid;
		gap: toRem(25);
		@media (min-width: $mobile) {
			grid-template-columns: 39.517975% 1fr;
		}
	}

	// .treatment__content

	&__content {
		@media (min-width: $mobile) {
			padding-top: toRem(30);
		}
		&.content {
			.content__title {
				color: var(--blue-color-600);
				@include adaptiveValue("font-size", 41, 25);
			}

			.content__text {
				color: var(--gray-color-200);
			}
		}
	}

	&__table {
		overflow-x: auto;
		table {
			width: 100%;
			border-collapse: collapse;
			// min-width: toRem(773);

			tr {
				td {
					height: toRem(60);
					@media (max-width: $mobile) {
						height: toRem(15);
					}
				}
			}
		}
		thead {
			font-size: toRem(16);
			line-height: 1.4;

			tr {
				th {
					padding: toRem(5) toRem(13);
					background-color: var(--brand-color);
					height: toRem(60);
					@media (max-width: $mobile) {
						height: toRem(15);
						@include adaptiveValue("font-size", 14, 6, 0, 767.98, 500);
					}
					@media (max-width: toEm(500)) {
						padding: toRem(5);
					}
				}
			}
		}
		tbody {
			tr:first-child,
			td:first-child {
				font-weight: 700;
				line-height: 1.6;
				padding: toRem(5) toRem(23);
				width: toRem(350);
				font-size: toRem(16);
				@media (max-width: $mobile) {
					padding: toRem(3) toRem(10);
					@include adaptiveValue("width", 300, 150, 0, 767.98, 500);
					@include adaptiveValue("font-size", 16, 6, 0, 767.98, 500);
				}
				@media (max-width: toEm(500)) {
					padding: toRem(5);
				}
			}
			tr {
				td {
					padding: toRem(5) toRem(16);
					@media (max-width: toEm(500)) {
						padding: toRem(5);
					}
					i {
						display: block;
						width: toRem(26);
						height: toRem(35);
						position: relative;
						@media (max-width: toEm(500)) {
							width: toRem(10);
							height: toRem(14);
						}
						img {
							position: absolute;
							width: 100%;
							height: 100%;
							top: 0;
							left: 0;
							object-fit: contain;
						}
					}
				}
			}
		}
		th,
		td {
			border: toRem(2) solid var(--white-color-400);

			text-align: left;
		}
		th {
			color: white;
		}
	}
}
