.advantages-item {
   @media (min-width: $mobile) {
      grid-row: 1 / 2;
      &:nth-child(1) {
         grid-column: 1 / 3;
      }
      &:nth-child(2) {
         grid-column: 2 / 5;
      }
      &:nth-child(3) {
         grid-column: 4 / 6;
      }
   }
   @media (max-width: $mobile) {
      max-width: 60%;
      min-width: toRem(264);
      margin: 0 auto;
      width: 100%;
   }

   // .advantages-item__body

   &__body {
      position: relative;
      padding-bottom: 81.223629%; /* 385/474 */
      img {
         width: 100%;
         height: 100%;
      }
   }

   // .advantages-item__text

   &__text {
      padding: 35%;
      display: flex;
      align-items: center;
      justify-content: center;
      right: 0;
      bottom: 0;
      position: absolute;
      clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
      overflow: hidden;
      // padding-bottom: 100%;
   }

   &__text-wrapper {
      --space-item: 25%;
      position: absolute;
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;
      gap: 0.6em;
      top: var(--space-item);
      left: var(--space-item);
      right: var(--space-item);
      bottom: var(--space-item);
   }

   // .advantages-item__title

   &__title {
      @include adaptiveValue("font-size", 24, 12, 0, 1440, 768);
      line-height: 1.45;
      @media (max-width: $mobile) {
         @include adaptiveValue("font-size", 24, 12, 0, 768, 480);
      }
   }

   // .advantages-item__subtitle

   &__subtitle {
      @include adaptiveValue("font-size", 17, 9, 0, 1440, 768);
      line-height: 1.5;
      @media (max-width: $mobile) {
         @include adaptiveValue("font-size", 17, 9, 0, 768, 480);
      }
   }
}

.advantages-item-decor {
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
}
