.image-block {
   --space: 140px;
   @media (max-width: $tablet) {
      --space: 100px;
   }
   @media (max-width: $mobile) {
      --space: 60px;
   }

   &.image-block--hero {
      position: relative;
      &::before {
         content: "";
         position: absolute;
         top: 0;
         right: 0;
         width: 42vw;
         height: 100%;
         background: url("@img/media-image-block/bg.jpg") center / cover no-repeat;
      }
      @media (max-width: toEm(641)) {
         &::before {
            bottom: 0;
            top: auto;
            width: 100%;
            height: 60%;
            background: linear-gradient(180deg, rgba(241, 248, 252, 0) 23.82%, rgba(241, 248, 252, 0.9) 31.31%);
         }
      }
   }
   // .image-block__container

   &__container {
   }

   // .image-block__body

   &__body {
      padding: var(--space) 0;
      display: grid;
      align-items: center;
      grid-template-columns: repeat(2, 1fr);
      gap: toRem(24);
      position: relative;
      .image-block--hero & {
         &::before {
            content: "";
            top: calc(var(--space) / 2);
            position: absolute;
            pointer-events: none;
            z-index: 3;
            width: toRem(124);
            height: toRem(570);
            left: toRem(-40);
            background: url("@img/media-image-block/gradient.svg") 0 0 / contain no-repeat;
            @media (max-width: toEm(1390)) {
               left: toRem(-25);
            }
         }
         @media (max-width: $mobile) {
            &::before {
               width: toRem(89);
               left: toRem(0);
            }
         }
         @media (max-width: toEm(641)) {
            padding: var(--space) 0 toRem(20) 0;
            &::before {
               width: toRem(59);
               top: toRem(20);
            }
         }
      }
      @media (max-width: toEm(641)) {
         grid-template-columns: repeat(1, 1fr);
         gap: toRem(60);
      }
   }
}

.content-image-block {
   padding: toRem(20) 0;
   height: 100%;
   display: flex;
   gap: toRem(10);
   flex-direction: column;
   justify-content: center;
   align-items: center;
   text-align: center;
   max-width: toRem(400);
   margin: 0 auto;
   @media (min-width: toEm(642)) {
      align-items: start;
      max-width: 100%;
      margin: 0;
      text-align: left;
      .image-block--reverse & {
         grid-column: 2/3;
         grid-row: 1/2;
      }
   }

   .s-subtitle {
      .image-block--hero & {
         line-height: 1.345;
         @include adaptiveValue("font-size", 27, 13, 0, 1390, 641);
         @media (max-width: $mobile) {
            @include adaptiveValue("font-size", 20, 13, 1, 641, 402);
         }
      }
   }
   // padding-right: toRem(30);
}

.media-image-block {
   @media (min-width: toEm(642)) {
      .image-block--reverse & {
         grid-column: 1/2;
         grid-row: 1/2;
      }
   }
}
