.form {
	display: grid;
	gap: toRem(27);
	@media (max-width: $mobile) {
		gap: toRem(11);
	}

	.form__row {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: toRem(35);
		@media (max-width: $mobile) {
			gap: toRem(15);
		}
	}

	// .form__line

	&__line {
	}

	&__label {
		display: block;
		font-size: toRem(16);
		margin-bottom: toRem(10);
	}

	// .form__input

	&__input {
	}

	// .form__send

	&__send {
		margin-right: auto;

		@media (max-width: $mobile) {
			margin: 0 auto;
		}
	}
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.input {
	width: 100%;
	display: block;
	font-size: toRem(16);
	padding: 0 toRem(16);
	height: toRem(47);
	background-color: var(--white-color);
	transition: all 0.3s;
	color: var(--brand-color);
	outline: toRem(1) solid var(--brand-color);
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding: 0 toRem(12);
	}

	&:focus {
		outline-color: var(--brand-color);
	}
	@media (max-width: $mobileSmall) {
		height: toRem(30);
	}

	&::placeholder {
		color: var(--gray-color-100);
	}
}

.input[placeholder]:not([data-placeholder-nohiden]) {
	&::placeholder {
		transition: opacity 0.3s;
	}
	&:focus::placeholder {
		opacity: 0;
	}
}

textarea.input {
	resize: vertical;
	padding: toRem(16);
	min-height: toRem(82);
	@media (max-width: $mobileSmall) {
		padding: toRem(12);
	}
}
