.testimonials {
	padding-top: toRem(32);
	padding-bottom: toRem(60);
	@media (max-width: $mobile) {
		padding-top: toRem(15);
		padding-bottom: toRem(30);
	}

	// .testimonials__container

	&__container {
	}

	// .testimonials__content

	&__content {
		max-width: toRem(653);
		margin: 0 auto;
		&:not(:last-child) {
			margin-bottom: toRem(65);
			@media (max-width: $mobile) {
				margin-bottom: toRem(35);
			}
		}
		&.content {
			.content__title {
			}
		}
	}

	&__items {
		display: grid;
		@include adaptiveValue("gap", 77, 20, 0, $containerWidth, 991.98);
		@media (min-width: $tablet) {
			grid-template-columns: repeat(3, 1fr);
		}
	}
}

.item-testimonial {
	display: flex;
	flex-direction: column;
	gap: toRem(25);
	padding: toRem(25);
	@media (max-width: $tablet) {
		padding: toRem(15);
	}
	@media (max-width: $mobileSmall) {
		gap: toRem(15);
	}
	// .item-testimonial__top

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: toRem(10);
	}

	// .item-testimonial__profile

	&__profile {
	}

	// .item-testimonial__company

	&__company {
		flex: 0 0 toRem(50);
		width: toRem(50);
		height: toRem(50);

		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(40);
			width: toRem(40);
			height: toRem(40);
		}
	}

	// .item-testimonial__rating

	&__rating {
	}

	// .item-testimonial__quote

	&__quote {
		font-size: toRem(20);
		line-height: 1.4;
		color: var(--gray-color-300);
		&::before,
		&::after {
			content: '"';
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
}
.profile {
	display: flex;
	align-items: center;
	gap: toRem(18);
	@media (max-width: $mobileSmall) {
		gap: toRem(15);
	}
	// .profile__avatar

	&__avatar {
		flex: 0 0 toRem(50);
		width: toRem(50);
		height: toRem(50);
		border-radius: 50%;
		border: toRem(1) solid var(--black-color);
		overflow: hidden;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(40);
			width: toRem(40);
			height: toRem(40);
		}
	}

	// .profile__body

	&__body {
		display: grid;
		gap: toRem(5);
		color: var(--gray-color-400);
	}

	// .profile__name

	&__name {
		font-size: toRem(20);
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}

	// .profile__visit

	&__visit {
		font-size: toRem(15);
		@media (max-width: $mobileSmall) {
			font-size: toRem(12);
		}
	}
}

.ratiing {
	display: flex;
	align-items: center;
	gap: toRem(3);
	// .ratiing__item

	&__item {
		flex: 0 0 toRem(20);
		width: toRem(20);
		height: toRem(20);
		img {
			max-width: 100%;
		}
	}
}
