.programs {
	&:not(:last-child) {
		@media (min-width: $mobile) {
			// margin-bottom: toRem(80);
		}
	}
	// .programs__container

	&__container {
	}

	&__body {
		position: relative;
		padding-top: toRem(30);
		@media (min-width: $tablet) {
			padding-bottom: toRem(278);
		}

		@media (min-width: $mobile) and (max-width: $tablet) {
			padding-bottom: toRem(238);
		}
		@media (max-width: $mobile) {
			padding-bottom: toRem(45);
		}
		&::before,
		&::after {
			content: "";
			position: absolute;
			width: 100vw;
			top: 0;
			height: 100%;
			background-color: var(--brand-color);
			z-index: -1;
		}
		&::after {
			right: 0;
		}
		&::before {
			left: 0;
		}
	}

	&__decor {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		&::before,
		&::after {
			content: "";
			position: absolute;
			width: 100vw;
			height: 100%;
			top: 0;
			background: url("@img/bg-decor.png") 0 0 / cover no-repeat;
		}
		&::after {
			right: 0;
		}
		&::before {
			left: 0;
		}
		@media (max-width: $mobile) {
			display: none;
		}
	}

	// .programs__header

	&__header {
		max-width: toRem(755);
		margin: 0 auto;
		&.content {
			color: var(--white-color);
			.content__sub-title {
				font-weight: 400;
				&:not(:last-child) {
					margin-bottom: toRem(10);
				}
			}
			.content__title {
			}
			.content__text {
			}
		}
	}

	// .programs__items

	&__items {
		display: grid;
		@include adaptiveValue("gap", 57, 20, 0, $containerWidth, 767.98);
		@media (min-width: $tablet) {
			margin-top: toRem(-226.5);
		}
		@media (min-width: $mobile) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media (min-width: $mobile) and (max-width: $tablet) {
			margin-top: toRem(-187);
		}
		@media (max-width: $mobile) {
			gap: toRem(0);
			margin: 0 toRem(-20);
		}
	}
}

.item-program {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: var(--white-light-color);
	padding: toRem(53) toRem(25) toRem(35) toRem(25);
	text-align: center;
	@media (max-width: $tablet) {
		padding: toRem(30) toRem(25) toRem(30) toRem(25);
	}
	@media (max-width: $mobile) {
		padding: toRem(10) toRem(20);
		&:first-child {
			padding-top: toRem(40);
		}
		&:last-child {
			padding-bottom: toRem(30);
		}
	}
	// .item-program__icon

	&__icon {
		flex: 0 0 toRem(160);
		width: toRem(160);
		height: toRem(160);
		border-radius: 50%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		@media (max-width: $tablet) {
			flex: 0 0 toRem(100);
			width: toRem(100);
			height: toRem(100);
		}
		&:not(:last-child) {
			margin-bottom: toRem(15);
			@media (max-width: $mobile) {
				margin-bottom: toRem(12);
			}
		}
		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: linear-gradient(180deg, #499afa 0%, rgba(222, 237, 254, 0.5) 50.5%, rgba(90, 164, 250, 0.94) 100%);
			opacity: 0.1;
			z-index: 1;
			border-radius: inherit;
		}
		img {
			width: toRem(132);
			height: toRem(132);
			object-fit: contain;
			position: relative;
			z-index: 2;
			@media (max-width: $tablet) {
				width: toRem(72);
				height: toRem(72);
			}
		}
	}

	// .item-program__body

	&__body {
		flex: 1 1 auto;
		&:not(:last-child) {
			margin-bottom: toRem(30);
			@media (max-width: $mobile) {
				margin-bottom: toRem(23);
			}
		}
		@media (max-width: $mobile) {
			max-width: toRem(300);
			margin: 0 auto;
		}
	}

	// .item-program__title

	&__title {
		color: var(--blue-color-700);
		font-size: toRem(25);
		line-height: 1.2;
		max-width: toRem(200);
		margin: 0 auto;
		@media (max-width: $tablet) {
			font-size: toRem(22);
		}
		@media (max-width: $mobile) {
			font-size: toRem(25);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(21);
		}
		&:not(:last-child) {
			margin-bottom: toRem(25);
			@media (max-width: $mobile) {
				margin-bottom: toRem(20);
			}
		}
	}

	// .item-program__text

	&__text {
		color: var(--blue-color-200);
		font-size: toRem(17);
		line-height: 1.5;
		@media (max-width: $tablet) {
			font-size: toRem(14);
		}
		@media (max-width: $mobile) {
			font-size: toRem(18);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(12);
		}
	}

	// .item-program__decor

	&__decor {
		flex: 0 0 toRem(48);
		width: toRem(48);
		height: toRem(48);
		border-radius: 50%;
		background-color: rgba(73, 154, 250, 0.1);
		display: flex;
		justify-content: center;
		align-items: center;
		&::before {
			content: "+";

			color: var(--brand-color);
		}
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(40);
			width: toRem(40);
			height: toRem(40);
		}
	}

	&:nth-child(1) {
		.item-program__icon {
			img {
				width: toRem(79);
				height: toRem(118);
				@media (max-width: $tablet) {
					height: toRem(58);
				}
			}
		}
	}
}
