.advantages {
	padding-top: toRem(70);
	padding-bottom: toRem(100);
	@media (max-width: $mobile) {
		padding-top: toRem(30);
		padding-bottom: toRem(30);
	}
	background-color: var(--primary-color);
	// .advantages__container

	&__container {
	}

	// .advantages__content

	&__header {
		&:not(:last-child) {
			margin-bottom: toRem(74);
		}
		&.content {
			.content__title {
				margin: 0 auto;
				max-width: 49%;
				min-width: toRem(322);
				@media (max-width: toEm(400)) {
					min-width: toRem(282);
				}
			}

			.content__text {
				margin: 0 auto;
				max-width: 55%;
				min-width: toRem(322);
				@include adaptiveValue("font-size", 19, 9, 0, 1440, 768);
				@media (max-width: toEm(400)) {
					min-width: toRem(282);
				}
			}
		}
	}
}

.advantages-list {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	@media (min-width: $mobile) {
		grid-template-columns: 31.755% 4.5% 27.45% 4.5% 31.755%;
		grid-template-rows: 1fr;
	}

	@media (max-width: $mobile) {
		gap: toRem(35);
	}
}
