.faq {
	padding-top: toRem(35);
	padding-bottom: toRem(100);
	@media (max-width: $mobile) {
		padding-bottom: toRem(50);
		padding-bottom: toRem(50);
	}
	@media (max-width: $mobileSmall) {
		padding-bottom: toRem(30);
		padding-bottom: toRem(30);
	}

	// .faq__container

	&__container {
		display: grid;
		align-items: start;
		@include adaptiveValue("gap", 62, 30, 0, $containerWidth, 991.98);
		@media (min-width: $tablet) {
			grid-template-columns: percent(590, $containerWidth) 1fr;
		}
	}

	&__body {
		display: grid;
		gap: toRem(40);

		@media (min-width: $tablet) {
			max-width: toRem(684);
		}

		@media (max-width: $mobile) {
			gap: toRem(18);
		}
	}

	// .faq__content

	&__content {
		&.content {
			.content__title {
				color: var(--black-color);
				font-weight: 600;
			}
			.content__text {
				font-size: toRem(19);
				@media (max-width: $mobileSmall) {
					font-size: toRem(12);
				}
			}
		}
	}

	&__image-wrapp {
		position: relative;
		@media (max-width: $tablet) {
			display: flex;
			justify-content: center;
			& > * {
				width: 100%;
			}
		}
		&::before,
		&::after {
			content: "";
			position: absolute;
			background: url("@img/decors/decor-dots.png") 0 0 no-repeat;
			width: 26.913858%;
			height: 18.790451%;
			z-index: -1;
		}
		&::before {
			top: 42%;
			right: toRem(-60);
		}
		&::after {
			bottom: -6%;
			left: -14%;
		}
	}

	&__image {
		padding-bottom: percent(534, 377);
		position: relative;
		z-index: 2;
		display: none;
	}
}

// .faq__spollers

.spollers {
	display: grid;
}
// 
.spollers__item {
	&:first-child {
		border-top: toRem(1) solid var(--black-color);
	}
	border-bottom: toRem(1) solid var(--black-color);
}
//  
.spollers__title {
	font-size: toRem(20);
	display: flex;
	justify-content: space-between;
	align-items: start;
	gap: toRem(18);

	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(15) toRem(10) toRem(15) 0;
	list-style: none;
	line-height: 1.3;
	transition: color 0.3s;
	@media (max-width: $mobileSmall) {
		font-size: toRem(10);
		padding: toRem(10) toRem(10) toRem(10) 0;
	}
	@media (any-hover: hover) {
		&:hover {
			color: var(--hover--color);
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}

	&._spoller-active {
		color: var(--brand-color);
		.spollers__icon {
			&::after {
				transform: rotate(90deg);
			}
		}
	}
}
.spollers__icon {
	flex: 0 0 toRem(12);
	width: toRem(12);
	height: toRem(12);
	margin-top: 0.4em;
	transition: transform 0.3s;
	position: relative;

	@media (max-width: $mobileSmall) {
		margin-top: 0.15em;
		width: toRem(11);
		height: toRem(11);
	}

	&::before,
	&::after {
		content: "";
		transition:
			background-color 0.3s,
			transform 0.3s;
		position: absolute;
		background-color: var(--brand-color);
	}
	&::before {
		top: 50%;
		transform: translateY(-50%);
		height: toRem(1);
		width: 100%;
	}
	&::after {
		height: 100%;
		width: toRem(1);
		left: 50%;
		transform: translateX(-50%);
		transition:
			transform 0.3s,
			background-color 0.3s;
	}

	._spoller-active & {
		&::after {
			transform: rotate(90deg);
		}
	}
}

//  
.spollers__body {
	padding: toRem(0) toRem(0) toRem(18) toRem(0);
	font-size: toRem(20);
	line-height: 1.28;
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding: toRem(0) toRem(0) toRem(18) toRem(0);
	}
}
